import React, { Component } from "react";
import "./RecordBtn.scss";
const constraints = {
  audio: true,
  video: false,
};
async function getStream() {
  try {
    let stream = await navigator.mediaDevices.getUserMedia(constraints);
    window.rec = new MediaRecorder(stream);
  } catch (err) {
    console.log(err);
  }
}
let chunks = [];

getStream();

export class RecordBtn extends Component {
  record = (e) => {
    if (!e.target.classList.contains("pulse")) {
      e.target.classList.add("pulse");
      window.rec.start();
      console.log("recording started");
    } else {
      e.target.classList.remove("pulse");
      window.rec.stop();
      console.log("recording stopped");
    }

    window.rec.ondataavailable = (e) => {
      chunks.push(e.data);
    };
    window.rec.onstop = () => {
      let blob = new Blob(chunks, { type: "audio/mp3" });
      let audioURL = URL.createObjectURL(blob);
      chunks = [];
      console.log(blob, audioURL);
    };
  };

  render() {
    return (
      <div
        onMouseDown={this.record}
        onMouseUp={this.record}
        onTouchStart={this.record}
        onTouchEnd={this.record}
        className='record-btn text-uppercase bg-danger mx-auto text-light'
      >
        rec
      </div>
    );
  }
}

export default RecordBtn;
